#Home{
    background: url('https://media.giphy.com/media/y3wBjXMffuFjy/source.gif');
    background-repeat: no-repeat, repeat;
    background-size: 100% 100%;
    background-color: #00000017;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#Home span{
    padding: 0 20px 0 20px;
    text-align: center;
}

#Home .greeting-tilte{
    font-weight: 700;
    font-size: 30px;
}

#Home .greeting-subtilte{
    font-family: 'Times New Roman', Times, serif;
    margin-top: 10px;
    font-size: 20px;
}
